.image.osc {
    width:150px;
    height:150px;
    border-radius:50%;
    overflow:hidden;
    position:relative;
    z-index:1;
    @mixin desktop {
        width:200px;
        height:200px;
    }
    figure {
        width:100%;
        height:100%;
    }
    img {
        height:100%;
        width:100%;
        object-fit: cover;
        object-position:center center;
        @mixin desktop {
            object-position:center center;
        }
    }
}